body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 1000px;
  color: #555;
}

.profile-avatar{
  width: 20%;
}

.menu{
  position: fixed;
  padding: 0;
  height: 0;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 70px;
  transition: all .5s;
}
.mobile-menu {
  position: fixed;
  top: 20px;
  left: 0;
  width: 0;
  height: 0;
  overflow: hidden;
  text-align: center;
  z-index: 9999;
  transition: all .3s ease;
  padding: 0;
  background-color: rgba(0,0,0,.8);
  opacity: 0;
}

.mobile-menu.active {
   height: 100%;
   width: 100%;
   top: 0;
   opacity: 1;
 }

.menu.menu--active .menu__mobile-button {
  color: #555 !important;
  opacity: .9;
  margin-top: -5px;
  transition: all .3s;
  background-color: transparent;
  border: none;
}

.mobile-menu .mobile-menu__close {
  color: #fff;
  transition: all .3s;
  position: absolute;
  top: 50px;
  background-color: transparent;
  border: none;
}

.mobile-menu.mobile-menu--active .mobile-menu__close {
  top: 25px !important;
}

.mobile-menu .mobile-menu__wrapper {
  height: 100%;
  overflow: auto;
  font-size: 14px;
  line-height: 20px;
  font-family: Roboto Mono,monospace;
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 60px;
}

.mobile-menu .mobile-menu__wrapper ul li {
  list-style: none;
  padding: 6px 0;
}

.mobile-menu .mobile-menu__wrapper ul li a::after {
  background: #fff;
  bottom: 14px;
  right: 1px;
}

.mobile-menu .mobile-menu__wrapper ul li a {
  color: #fff !important;
  display: inline-block;
  padding: 11px;
  position: relative;
}

.mobile-menu .mobile-menu__wrapper ul {
  padding: 30px 0;
  margin: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  min-height: 100%;
}
.menu .menu__wrapper {
  z-index: 1;
  margin-top: 50px;
  transition: all .3s;
}

.menu .menu__wrapper nav {
  font-size: 14px;
  line-height: 20px;
  font-family: Roboto Mono,monospace;
  text-transform: uppercase;
  font-weight: 700;
}
nav {
  font-size: 12px;
  line-height: 18px;
  font-family: Roboto Mono,monospace;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 30px;
}
nav ul li {
  display: inline-block;
}

.menu .menu__wrapper nav a {
  color: #fff !important;
  opacity: 1;
}

.menu .menu__wrapper nav a:hover {
  opacity: .5;
}

menu.menu--active .menu__wrapper a:hover {
  color: #6d56c1!important;
}

nav ul li a {
  padding-right: 50px;
  color: #999 !important;
}

nav ul li a:hover:after {
  opacity: 1;
}

nav ul li a:after {
  content: "";
  position: absolute;
  width: 7px;
  height: 2px;
  bottom: 0;
  right: 42px;
  opacity: 0;
  transition: background .3s;
  background: #6d56c1;
}

nav ul li a:hover {
  text-decoration: none;
  transition: all .15s;
  color: #6d56c1 !important;
}

.menu .menu__wrapper nav a {
  color: #fff;
  opacity: 1;
}

a {
  color: #6d56c1;
  transition: all .3s;
  position: relative;
  text-decoration: none;
  cursor: pointer;
}
.menu .menu__wrapper nav a:after {
  background: #fff;
}

.menu.menu--active .menu__wrapper a:hover {
  color: #6d56c1!important;
}

ol, ul {
  padding: 0;
  list-style: none;
}

.menu .menu__mobile-button {
  color: #fff;
  opacity: .5;
  transition: all .3s;
  background-color: transparent;
  border: none;
}
.menu.menu--active {
  background-color: #fff;
  box-shadow: 0 5px 8px 0 rgba(0,0,0,.14);
}
.menu.menu--active .menu__wrapper a {
  color: #555!important;
  opacity: 1;
}

.menu.menu--active .menu__wrapper a:after {
  background: #6d56c1!important;
}
.menu.menu--active .menu__wrapper {
  margin-top: 25px;
}

.top-section{
  background-image: url("./bg-main.jpg");
  background-color: #6d56c1;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: cover;
  position: relative;
  margin-bottom: 40px;
}

.top-section:before{
  background: linear-gradient(54deg,rgba(74,76,179,.95) 34%,hsla(0,34%,71%,.95));
  height: 100%;
  left: 0;
  right: 0;
  position: absolute;
  content: "";
  z-index: 0;
}

.personal-profile {
  color: #fff;
  font-family: Roboto Mono,monospace;
  padding-top: 90px;
}

.personal-profile .personal-profile__avatar img {
  border-radius: 5px;
  box-shadow: 0 0 27px rgba(96,96,96,.34);
  -o-object-fit: cover;
  object-fit: cover;
  margin-top: 10px;
  position: relative;
  top: 40px;
  -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
  filter: grayscale(100%);
}

.personal-profile .personal-profile__name {
  font-size: 44px;
  line-height: 50px;
  font-weight: 700;
  margin-top: 40px;
  margin-bottom: 10px;
}

.personal-profile .personal-profile__work {
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 40px;
}

.personal-profile .personal-profile__contacts {
  margin-bottom: 20px;
}

.contact-list {
  font-size: 16px;
  line-height: 24px;
  font-family: Roboto Mono,monospace;
}

.contact-list.contact-list__opacity-titles dt {
  opacity: .4;
}

.contact-list dt {
  float: left;
  font-weight: 700;
  text-transform: uppercase;
}
.contact-list dd {
  padding-left: 100px;
  margin-bottom: 15px;
}

.contact-list dd a {
  color: #fff;
}

.personal-profile .personal-profile__social {
  margin-bottom: 35px;
}

.personal-profile .personal-profile__social a {
  font-size: 24px;
  margin-right: 15px;
  opacity: .4;
  color: #fff!important;
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

.site-btn {
  background-color: #6d56c1 !important;
  height: 50px;
  border-radius: 25px !important;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  padding: 15px;
  min-width: 170px;
  text-align: center;
  display: inline-block;
  color: #fff!important;
  font-weight: 600;
  overflow: hidden;
  outline: 0;
}

.MuiButtonBase-root{
  border-radius: 25px !important;
}

.site-btn:focus, .site-btn:hover {
  background-color: #7b67c7;
  text-decoration: none;
  outline: 0;
}

.site-btn .download-icon {
  height: 14px;
  margin-top: -5px;
  margin-right: 5px;
}

.section .section__title {
  margin-bottom: 40px;
  font-size: 36px;
  line-height: 42px;
  font-family: Roboto Mono,monospace;
}

.section .section__description {
  margin-bottom: 30px;
}

.resume-list {
  margin-top: 30px;
  margin-bottom: 30px;
}

.resume-list .resume-list_title {
  font-family: Roboto Mono,monospace;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 5px;
  text-transform: uppercase;
  font-weight: 700;
  color: #999;
  margin-bottom: 40px;
}

.resume-list .resume-list__block {
  border-left: 2px solid #6d56c1;
  padding-left: 25px;
  padding-bottom: 50px;
}

.resume-list .resume-list__block:before {
  width: 12px;
  height: 12px;
  border: 2px solid #6d56c1;
  position: absolute;
  content: "";
  border-radius: 50%;
  background-color: #fff;
  left: 10px;
}

.resume-list .resume-list__block p {
  margin: 0;
}

.resume-list .resume-list__block-title {
  text-transform: uppercase;
  color: #6d56c1;
  font-size: 14px;
  line-height: 20px;
  font-family: Roboto Mono,monospace;
  font-weight: 700;
  padding-bottom: 5px;
}

.resume-list .resume-list__block-date {
  font-family: Inconsolata,monospace;
  font-size: 14px;
  line-height: 20px;
  color: #999;
  padding-bottom: 10px;
}

.resume-list .resume-list__block:last-child {
  padding-bottom: 0;
}

.progress-list {
  margin-top: 30px;
}

.progress-list .progress-list__title, .progress-list p {
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
}

.progress-list .progress-list__title {
  font-family: Roboto Mono,monospace;
  letter-spacing: 5px;
  font-weight: 700;
  color: #999;
  margin-bottom: 40px;
}

.progress-list p {
  width: 100%;
  font-family: Inconsolata,monospace;
  margin-bottom: 10px;
}

.progress-list p .progress-list__skill-value {
  float: right;
}

.progress-list .progress {
  height: 6px;
  margin-bottom: 30px;
}

.progress-list .progress .progress-bar {
  background-color: #6d56c1;
}
.progress-bar {
  height: 1rem;
  line-height: 1rem;
  color: #fff;
  background-color: #007bff;
  transition: width .6s ease;
}

.portfolio-menu .portfolio-menu__link--active {
  color: #6d56c1 !important;
  text-decoration: none;
}

.project-card {
  margin-left: 0;
  margin-bottom: 40px;
  transition: box-shadow .3s;
  margin-right: 0;
}
.project-card:hover {
  box-shadow: 0 1px 31px rgba(0,0,0,.09);
  cursor: pointer;
}

.project-card .project-card__img {
  padding-right: 0;
  padding-left: 0;
}

.project-card .project-card__img img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
}

.project-card .project-card__info {
  border: 1px solid #f1f1f1;
  padding: 50px 50px 40px 30px;
}

.project-card .project-card__title {
  font-size: 18px;
  line-height: 30px;
  font-family: Roboto Mono,monospace;
  text-transform: uppercase;
  margin-bottom: 15px;
  font-weight: 700;
}

.project-card .project-card__description {
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 45px;
}

.project-card .project-card__stack {
  font-family: Roboto Mono,monospace;
  font-size: 12px;
  line-height: 18px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 15px;
}

.tags li {
  display: inline-block;
  font-size: 14px;
  line-height: 20px;
  background-color: #fff;
  box-shadow: 0 1px 4px rgba(0,0,0,.14);
  padding: 5px 10px;
  margin-right: 10px;
  border-radius: 3px;
}

.project-card .project-card__link {
  margin-top: 55px;
  font-family: Roboto Mono,monospace;
  font-size: 14px;
  line-height: 20px;
  float: right;
}

.background {
  background-image: url("./img_bg_main.jpg");
  background-color: #6d56c1;
  height: 100%;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: cover;
  position: relative;
  color: #fff;
}

.background:before {
  background: linear-gradient(54deg,rgba(74,76,179,.95) 34%,hsla(0,34%,71%,.95));
  height: 100%;
  left: 0;
  right: 0;
  position: absolute;
  content: "";
  z-index: 0;
}

.contacts {
  margin-top: 20px;
}

.contacts .contacts__list {
  margin-bottom: 45px;
}

.contact-list {
  font-size: 16px;
  line-height: 24px;
  font-family: Roboto Mono,monospace;
}

.contact-list dt {
  float: left;
  font-weight: 700;
  text-transform: uppercase;
}

.contact-list dd {
  padding-left: 100px;
  margin-bottom: 15px;
}

.contact-list dd a {
  color: #fff;
}

.contacts .contacts__social {
  font-size: 16px;
  line-height: 24px;
}

.contacts .contacts__social li {
  margin-bottom: 20px;
}

.contacts .contacts__social a {
  color: #fff;
  text-decoration: underline;
  padding: 6px 0;
  transition: all .3s;
}

.contacts .contacts__form-title, .footer {
  font-size: 16px;
  line-height: 24px;
  font-family: Roboto Mono,monospace;
}

.contacts .contacts__form-title {
  margin-bottom: 20px;
}

.contacts .contacts__form .form-group {
  margin-bottom: 15px;
  position: relative;
}

.contacts .contacts__form input {
  width: 100%;
  background-color: transparent;
  border-radius: 3px;
  border: 1px solid #fff;
  padding: 10px;
  color: #fff;
  font-size: 16px;
  line-height: 24px;
  font-family: Roboto Mono,monospace;
}

.contacts .contacts__form .form-group .form-validation {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 2px;
  border: 2px solid #6d56c1;
  pointer-events: none;
  opacity: 0;
  transition: all .3s;
}

.contacts .contacts__form .form-group span {
  color: #fff;
  position: absolute;
  top: 10px;
  text-align: right;
  opacity: 0;
  right: 20px;
  z-index: 0;
}

.mdi-close:before {
  content: "\F156";
}

.mdi:before, .mdi-set {
  display: inline-block;
  font: normal normal normal 24px/1 "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  line-height: inherit;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.contacts .contacts__form input:invalid {
  outline: none;
}

.contacts .contacts__form input[data-touched=true]:invalid~.form-invalid-icon {
  opacity: 1;
}

.contacts .contacts__form .form-group span {
   color: #fff;
   position: absolute;
   top: 10px;
   text-align: right;
   opacity: 0;
   right: 20px;
   z-index: 0;
 }

.contacts .contacts__form textarea:invalid {
  outline: none;
}

.contacts .contacts__form textarea {
  width: 100%;
  background-color: transparent;
  border-radius: 3px;
  border: 1px solid #fff;
  padding: 10px;
  height: 115px;
  color: #fff;
  font-size: 16px;
  line-height: 24px;
  font-family: Roboto Mono,monospace;
}

.site-btn.site-btn--form {
  width: 100%;
  border: none!important;
  box-shadow: 0 1px 29px rgba(0,0,0,.16);
  position: relative;
}

.site-btn {
  background-color: #6d56c1;
  height: 50px;
  border-radius: 25px;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  padding: 15px;
  min-width: 170px;
  text-align: center;
  display: inline-block;
  color: #fff!important;
  font-weight: 600;
  overflow: hidden;
}

.contacts .contacts__form textarea:active, .contacts .contacts__form textarea:focus {
  box-shadow: inset 0 0 5px 0 #fff;
  border: 1px solid #fff;
  outline: none;
}

.contacts .contacts__form input:active, .contacts .contacts__form input:focus {
  box-shadow: inset 0 0 5px 0 #fff;
  border: 1px solid #fff;
  outline: none;
}

.site-btn.site-btn--form:active, .site-btn.site-btn--form:focus {
  box-shadow: none!important;
}

.site-btn.site-btn--form:hover {
  background-color: #7b67c7!important;
}

@media screen and (max-width:990px) {
  .personal-profile .personal-profile__avatar {
    display:none
  }
}

@media screen and (max-width:576px) {
  .portfolio-menu a {
    padding-right:15px
  }
  .portfolio-menu a:after {
    right:8px;
  }
}

